.App {
  font-family: sans-serif;
  background: red;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.2);
  color: rgba(243, 24, 43, 0.849);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 100%;
  height: 100%
}

.Buttons {
  width: 40px;
}

.ClockController {
  position: fixed;
  
  margin: auto;
  top: 0px;
  width: 20px;
  height: 20px;
  z-index: 2003;
  font-size: 20px; 
  -webkit-font-feature-settings: "kern" off; 
          font-feature-settings: "kern" off; 
  -webkit-font-kerning: none; 
          font-kerning: none;
  right: 15px;
  
}
.TimeLabel {
  position: fixed;
  margin: auto;
  background:rgba(77, 91, 129, 0);
  top: 0px;
  width: 400px;
  height: 80px;
  z-index: 2000;
  font-family: verdana;
  text-shadow: 2px 2px  rgba(43, 48, 5, 0.226);
  -webkit-font-feature-settings: "kern" off;
          font-feature-settings: "kern" off;
  -webkit-font-kerning: none;
          font-kerning: none;
  font-size: 20px; 
  left: 50%;
  margin-left: -200px;
}
.Fullscreen {
  position: fixed;
  z-index: 2000;
}
.TimeSwipe {
  position: fixed;
  bottom: 1px;
  background: rgb(77, 91, 129);
  width: 100%;
  z-index: 2001;
}
.TimeSelectorh {
  position: fixed;
  bottom: 0px;
  left: 0px;
  margin-left: 0px;
  width: 100%;
  height: 100px;
  z-index: 2001;
}
.TimeSelectorv {
  position: fixed;
  top: 0px;
  right: 0px;
  margin-top: 0px;
  width: 100px;
  height: 100%;
  z-index: 2001;
}

.Globe {
  position: fixed;
  background: black;
  top: 0;
  left:0px;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.Miniglobe {
  position: fixed;
  box-shadow: 2px 2px 5px rgba(37, 38, 39, 0.397);
  background:  linear-gradient(rgb(67, 124, 199), #111) repeat scroll 0 0 #222;
  height: 100px;
  width: 200px;
  top: 20px;
  left: 20px;
  z-index: 2; 
}

.TimeContainer {
    position: relative;
    background: rgba(26, 60, 99, 0.0);
    overflow: hidden;
 

}

.TimeScale {
    position: relative;
    white-space: nowrap;
    background: rgba(26, 60, 99, 0);
    font-family: verdana;
    text-shadow: 2px 2px  rgba(43, 48, 5, 0.226);
    -webkit-font-feature-settings: "kern" off;
            font-feature-settings: "kern" off;
    -webkit-font-kerning: none;
            font-kerning: none;

}

.DayTic {
    position: absolute;
    pointer-events: none;
    font-size: 12px; 
    bottom: 10px
}
.MonthTic {
    position: absolute;
    pointer-events: none;
    font-size: 15px; 
    bottom: 25px
}
.YearTic {
    position: absolute;
    pointer-events: none;
    font-size: 20px; 
    margin-left: 50px;
    bottom: 25px
}
.HourTic {
    position: absolute;
    pointer-events: none;
    font-size: 10px; 
    color: rgba(235, 214, 119, 0.5);
    top: 80px
}
.Mire {
    position: absolute;
    left: 50%;
    bottom: 0px;
    margin-left: -1px;
    width: 2px;
    height: 48px;
    background: rgba(181, 82, 219, 0.315);

}
.DayTic-v {
    position: absolute;
    pointer-events: none;
    font-size: 12px; 
    right: 10px
}
.MonthTic-v {
    position: absolute;
    pointer-events: none;
    font-size: 15px; 
    right: 30px
}
.YearTic-v {
    position: absolute;
    pointer-events: none;
    font-size: 20px; 
    margin-top: 30px;
    right: 30px
}
.HourTic-v {
    position: absolute;
    pointer-events: none;
    font-size: 10px; 
    color: rgba(235, 214, 119, 0.5);
    top: 80px
}
.Mire-v {
    position: absolute;
    top: 50%;
    right:0px;
    margin-top: 0px;
    width: 40px;
    height: 48px;
    background: rgba(181, 82, 219, 1)

}

